          | Planeación estratégica y financiera de alto valor. 

    p.mb-5 En la actualidad son muchas las estrategias para que los líderes de opinión especializados tengan en cuenta sobre el comportamiento de una corporación industrial y de servicios; sin embargo, siempre debe partir de un estudio profundo desde el interior de esta. Por lo anterior, a continuación, se desglosan algunos puntos clave que debe conocer para que las pueda incorporar en su labor profesional, a saber:

    TabsA.color-acento-contenido.mb-5
      .tarjeta.color-acento-contenido--borde.p-4(titulo="Verificar liderazgo")
        h4.mb-4 Verificar liderazgo
        .d-flex.flex-wrap.align-items-center
          .col-md.pe-md-4.order-2.order-md-1
            p.mb-0 La principal estrategia de estudiar los módulos estructurados de una industria es corroborar si poseen un líder para guiar convenientemente las distintas ocupaciones de la misma.
          img.order-1.order-md-2.mx-auto.mb-4.mb-md-0(src="@/assets/curso/temas/tema2/img-17.png" style="max-width: 210px")
      
      .tarjeta.color-acento-contenido--borde.p-4(titulo="Revisar actividades")
        h4.mb-4 Revisar actividades
        .d-flex.flex-wrap.align-items-center
          .col-md.pe-md-4.order-2.order-md-1
            p.mb-0 Luego se examinan las actividades que tienden a ejecutarse al interior de las áreas de la corporación y qué otras debemos diseñar o reestructurar en el organigrama funcional con su respectivo detalle y su correlación.  
          img.order-1.order-md-2.mx-auto.mb-4.mb-md-0(src="@/assets/curso/temas/tema2/img-18.png" style="max-width: 210px")
      
      .tarjeta.color-acento-contenido--borde.p-4(titulo="PASO 1: The splendor asd asd asd as dasd")
        h4.mb-4 Evaluación de colaboradores
        .d-flex.flex-wrap.align-items-center
          .col-md.pe-md-4.order-2.order-md-1
            p.mb-0 Posteriormente, reemplazamos las actividades utilizando los propios nombres de los colaboradores que ejecutan su labor en el ejercicio de cada una de esos oficios y de esta forma, instalaremos imágenes gráficas que, al cotejarlas entre sí, muestran un símbolo claro de las insuficiencias de los colaboradores de nuestra entidad y de la correcta distribución del mismo de acuerdo con las funciones propias de la misma.
          img.order-1.order-md-2.mx-auto.mb-4.mb-md-0(src="@/assets/curso/temas/tema2/img-19.png" style="max-width: 210px")
      
      .tarjeta.color-acento-contenido--borde.p-4(titulo="Planeación estratégica")
        h4.mb-4 Planeación estratégica de la gerencia
        .d-flex.flex-wrap.align-items-center
          .col-md.pe-md-4.order-2.order-md-1
            p.mb-0 Como se vio anteriormente, los diversos procesos y procedimientos se combinan, dando vía a la producción logística de bienes y servicios sostenibles, los cuales colaboran en la estructuración del organigrama de la corporación, por lo que cada departamento concreta una serie de ocupaciones con base en la función que desarrolla, su productividad y eficacia financiera, entonces dependen en gran medida de la participación en la planeación estratégica de la compañía. 
          img.order-1.order-md-2.mx-auto.mb-4.mb-md-0(src="@/assets/curso/temas/tema2/img-20.png" style="max-width: 210px")
      
      .tarjeta.color-acento-contenido--borde.p-4(titulo="Título por recibir")
        h4.mb-4 Título por recibir
        .d-flex.flex-wrap.align-items-center
          .col-md.pe-md-4.order-2.order-md-1
            p.mb-0 Como se vio anteriormente, los diversos procesos y procedimientos se combinan, dando vía a la producción logística de bienes y servicios sostenibles, los cuales colaboran en la estructuración del organigrama de la corporación, por lo que cada departamento concreta una serie de ocupaciones con base en la función que desarrolla, su productividad y eficacia financiera, entonces dependen en gran medida de la participación en la planeación estratégica de la compañía. 
          img.order-1.order-md-2.mx-auto.mb-4.mb-md-0(src="@/assets/curso/temas/tema2/img-21.png" style="max-width: 210px")
    
    p.mb-5 A continuación, se identifican algunas #[b funciones estándar en las corporaciones de producción y servicios que deben participar activamente en la documentación de procesos y procedimientos] cuando se hacen estudios dentro de las empresas, ya sean estas de producción o de servicios.

