    .edit-bg-text.edit-bg-color2.mb-5
      p.position-relative.col.mb-0 Para el desarrollo de la temática lo altamente importante es que el equipo de aprendices sea lo suficientemente ingenioso de crear su propia estructura organizacional, que vean la necesidad de acoplar sus conocimientos a las fallas actuales en las empresas que asesoren o trabajen, porque son idóneos para diseñar estratificaciones corporativas que sean eficientes y eficaces en sus resultados, incluyendo en su organigrama, operaciones, procesos, procedimientos y actividades elementos que se acoplen a la sostenibilidad del medio ambiente.
      img.position-relative(src="@/assets/curso/temas/tema2/img-23.svg" style="max-width: 85px")
    
    Separador

    .titulo-segundo.color-secundario
      h2#t_2_3 2.3 Registro de la información 
    
    .cajon-b.color-primario.edit-cajon-b.p-3.mb-5
      .d-flex.align-items-center
        img(src="@/assets/curso/temas/tema2/img-24.svg" style="max-width: 85px")
        p.mb-0.col.mb-0.ps-3 Acumular datos en una compañía ya no es un privilegio, sino una necesidad sentida que tienen las empresas; emplear infraestructuras tecnológicas de vanguardia, que permita medirlos por medio de indicadores de gestión, puede generar información clave y entregar resultados que están en tiempo presente, los que son llamados a ser soporte en la toma de decisiones para estructurar un completo sistema de gestión de la calidad.
    
    .d-flex.flex-wrap.mb-5.align-items-center
      .col-xl-8.order-2.order-xl-1
        p.mb-0 Los índices deben reflejar con precisión el esfuerzo que se realiza, se deben medir frecuentemente en el proceso durante desarrollo del programa de producción e ir implementando nuevas estrategias en función de los resultados para potenciar los objetivos esperados.
          br
          br
          |Las actuales condiciones de aumento de negocios por estructuras digitales, obligan a las empresas a contar con medios de registro de la información, como las bases de datos que resguardan de espías informáticos, información documentada relevante en el diario actuar de las corporaciones, el registro de la información es un protocolo de infraestructura tecnológica que guardan en medios magnéticos imágenes, formatos de Office y diferentes estructuras clave para la corporación y el cumplimiento a futuro de la planeación estratégica.
      img.col-xl-4.order-1.order-xl-2.mx-auto.mb-4.mb-xl-0(src="@/assets/curso/temas/tema2/img-25.svg" style="max-width: 400px")
    
    p.mb-5 Por otro lado, existen emprendimientos emergentes que almacenan cualquier cantidad de datos en la nube, lo cual se convierte en el soporte técnico y de servicio para las corporaciones.  Igual de efectivo, es guardar información en medios magnéticos, hardware ópticos o envío de información por red, infraestructuras que tienen el mismo funcionamiento que un cerebro humano, las cuales se soportan en dos formas estructurales de archivar, con la complejidad que amerita. 

    p.fw-bold.mb-5 Por ende, a través de la siguiente información se podrán conocer dos tipos de sistemas clave:

    .d-flex.flex-wrap.justify-content-center.mb-5
      .col-lg-6.col-xl-5.px-2.d-flex.mb-4
        .tarjeta(style="background-color: #FDF5E5; border-radius: 5px; overflow: hidden;")
          .p-4(style="background-color: #F0BD51;")
            img(src="@/assets/curso/temas/tema2/img-26.png")
          .p-4.py-md-5
            h4 Sistemas primarios
            p.mb-0Se encargan de tener la información temporalmente, estas alternativas se dan mientras esté en funcionamiento el servicio eléctrico, archivan datos de forma efímera, un ejemplo vivencial es las llamadas memorias (RAM) de un equipo de cómputo.
      
      .col-lg-6.col-xl-5.px-2.d-flex.mb-4
        .tarjeta(style="background-color: #F7E9E1; border-radius: 5px; overflow: hidden;")
          .p-4(style="background-color: #EF8B53;")
            img(src="@/assets/curso/temas/tema2/img-27.png")
          .p-4.py-md-5
            h4 Sistemas secundarios
            p.mb-0 De mucha mayor complejidad, son recomendados para tener en sus memorias la información de forma permanente, estas alternativas se dan así no esté en funcionamiento el servicio eléctrico, archivan cualquier cantidad de datos por el tiempo que se requiera, un ejemplo vivencial lo expresamos es los discos duros (SSD, HDD) de un equipo de cómputo.
    p.mb-5 Archivar información emplea como protocolo, utilizar distintos receptores de datos, estos eficientemente integran todas las áreas generales de la empresa, deben ser reconocidos y entendidos como soporte técnico, permanentemente empleados por todo el equipo de líderes y colaboradores de la compañía para unificar criterios en el manejo de la información, hablar el mismo lenguaje y alcanzar metas propuestas. 

    .bg-img-edit.mb-5.d-flex.flex-wrap.align-items-center(style="background-color: #583CB6")
      img.col-12.mx-auto(src="@/assets/curso/temas/tema1/img-7.svg" style="max-width: 190px; margin-top: -6rem")
      p.mb-4.mb-md-0.col-12.col-md.mb-0.texto-blanco.py-4 #[b ¡Importante!] El acopio de información hace parte a largo plazo de la estructura logística de una empresa, esto con el objetivo de tener acceso a la información cuando se le necesitare, uno de los objetivos más relevantes es garantizar a los negocios el uso de bases de datos en tiempo real, consultar fichas técnicas de materiales e insumos, stock de inventarios, generar ordene de compras, consultar base de datos de precios de venta, separar pedidos verificar pantone o cartas de colores, administrar procesos, en escena una estrategia eficaz eficiente y de alto valor agregado para las corporaciones.

    p.mb-5 Otro elemento de gran ayuda en la gestión administrativa de la información es que esta se puede transportar, administrar, transformar, distribuir y compartir en tiempo real, con altos protocolos de seguridad. Las entidades de forma genérica facilitan claves de seguridad para sus usuarios, quienes mediante estas pueden tener acceso a la siguiente información en general:

    .row.mb-5.justify-content-center
      .col-sm-8.col-xl-4.mb-4.mb-xl-0
        .tarjeta.tarjeta-slide.arriba.color-secundario(@mouseover="indicadorTarjetaSlide = false")
          .indicador--hover(v-if="indicadorTarjetaSlide")
          .tarjeta-slide__contenedor.d-flex.flex-wrap.align-content-center(style="aspect-ratio: 40/43;")
            .tarjeta-slide__contenido.p-4.p-xl-5
              p.mb-0 #[b Utilizar bases de datos de la empresa]
                | Encierra utilizar todas las actividades que tienen que ver con las obligaciones diarias para el cumplimiento de las funciones propias del puesto de trabajo. 
            .tarjeta-slide__img.d-flex.flex-wrap.align-items-center.justify-content-center(style="background-color: #FAEBCD")
              img.col-12.mx-auto(src="@/assets/curso/temas/tema2/img-28.svg" style="max-width: 144px;")
              .d-flex.justify-content-center.px-4.px-md-5.text-center.w-100
                h6(style="background-color: white; padding: .4rem 1rem; border-radius: 40px") Utilizar bases de datos de las empresas
      
      .col-sm-6.col-xl-4.mb-4.mb-xl-0
        .tarjeta.tarjeta-slide.arriba.color-secundario
          .tarjeta-slide__contenedor.d-flex.flex-wrap.align-content-center(style="aspect-ratio: 40/43;")
            .tarjeta-slide__contenido.p-4.p-xl-5
              p.mb-0 #[b Base de datos con información financiera]
                |Recabar en la utilización de datos financieros relevantes para su puesto de trabajo, balances, precios de ventas, valor de inventarios, entre otros.
            .tarjeta-slide__img.d-flex.flex-wrap.align-items-center.justify-content-center(style="background-color: #FAEBCD")
              img.col-12.mx-auto(src="@/assets/curso/temas/tema2/img-29.svg" style="max-width: 144px;")
              .d-flex.justify-content-center.px-4.px-md-5.text-center.w-100
                h6(style="background-color: white; padding: .4rem 1rem; border-radius: 40px") Base de datos con Información financiera
      
      .col-sm-6.col-xl-4.mb-4.mb-xl-0
        .tarjeta.tarjeta-slide.arriba.color-secundario
          .tarjeta-slide__contenedor.d-flex.flex-wrap.align-content-center(style="aspect-ratio: 40/43;")
            .tarjeta-slide__contenido.p-4.p-xl-5
              p.mb-0 #[b Caracterización de clientes]
                | Fuente de información de clientes internos, externos, códigos de identificación, correos electrónicos, redes sociales, datos telefónicos, entre otros.
            .tarjeta-slide__img.d-flex.flex-wrap.align-items-center.justify-content-center(style="background-color: #FAEBCD")
              img.col-12.mx-auto(src="@/assets/curso/temas/tema2/img-30.svg" style="max-width: 144px;")
              .d-flex.justify-content-center.px-4.px-md-5.text-center.w-100
                h6(style="background-color: white; padding: .4rem 1rem; border-radius: 40px") Caracterización de clientes

    p.mb-5 También es importante tener en cuenta en la logística de almacenamiento y registro de datos con otros elementos que son claves y que puede conocer a continuación:

    SlyderF.py-5(columnas="col-lg-6 col-xl-4")
      .tarjeta.p-4(style="background-color: #E2D9FF")
        .row.justify-content-center.mb-3
          .col-8
            img.mx-auto(src='@/assets/curso/temas/tema2/s-1.svg' style="max-width: 113px; margin-top: -80px")
        p #[b #[span(style="background-color: #E9BE64; padding: 2px 3px") 01.] Subir datos a la nube]
          br
          |Es el protocolo emergente de moda en el medio digital la información está en sitio seguro y se archiva en los servidores de una entidad externa, no copan los espacios de tus equipos, pero si puedes acceder a ella, 24 horas del día 7 días de la semana.
      
      .tarjeta.p-4(style="background-color: #E2D9FF")
        .row.justify-content-center.mb-3
          .col-8
            img.mx-auto(src='@/assets/curso/temas/tema2/s-2.svg' style="max-width: 113px; margin-top: -80px")
        p #[b #[span(style="background-color: #E9BE64; padding: 2px 3px") 02.] Empleo de datos de red]
          br
          |Uso comercial de datos de red, cuentas de servicio que brindan gran velocidad.
      .tarjeta.p-4(style="background-color: #E2D9FF")
        .row.justify-content-center.mb-3
          .col-8
            img.mx-auto(src='@/assets/curso/temas/tema2/s-3.svg' style="max-width: 113px; margin-top: -80px")
        p #[b #[span(style="background-color: #E9BE64; padding: 2px 3px") 03.] Empleo de archivo de datos de objeto]
          br
          | Se brinda el uso de carpetas, no es condición suficiente y necesaria darle jerarquía u ordenar los datos, omiten la creación de niveles de información.
      .tarjeta.p-4(style="background-color: #E2D9FF")
        .row.justify-content-center.mb-3
          .col-8
            img.mx-auto(src='@/assets/curso/temas/tema2/s-4.svg' style="max-width: 113px; margin-top: -80px")
        p #[b #[span(style="background-color: #E9BE64; padding: 2px 3px") 04.] Empleo de datos de archivo]
          br
          | Contrario al dato anterior, este jerarquiza y ordena la información, funciona como organizador de archivos.
      .tarjeta.p-4(style="background-color: #E2D9FF")
        .row.justify-content-center.mb-3
          .col-8
            img.mx-auto(src='@/assets/curso/temas/tema2/s-5.svg' style="max-width: 113px; margin-top: -80px")
        p #[b #[span(style="background-color: #E9BE64; padding: 2px 3px") 05.] Organizador de datos por bloque]
          br
          | Caracteriza los datos por áreas, para mayor facilidad de uso por los diferentes departamentos en las empresas.
      .tarjeta.p-4(style="background-color: #E2D9FF")
        .row.justify-content-center.mb-3
          .col-8
            img.mx-auto(src='@/assets/curso/temas/tema2/s-6.svg' style="max-width: 113px; margin-top: -80px")
        p #[b #[span(style="background-color: #E9BE64; padding: 2px 3px") 06.] Guardar datos por intermedio de software especializados]
          br
          | Cuando la empresa cuenta con el archivo de la información personalizada, construye sistemas que se acoplan a las necesidades logísticas de la empresa.
    
    p.mb-5 #[b Cualquier elemento empleado en recopilar datos de interés para su uso en procesos logísticos de producción o servicios o investigativo.]
      br
      br
      |El objetivo siempre es que el registro de la información sea confiable, seguro, que brinde garantías y que sea válido para la toma de decisiones, aunque se puede optar por enrutar la temática con miras a donde se obtenga alta cantidad de datos, para fortificar el abordaje de una solución de una necesidad o problema determinado a tratar.
